import {Context, useContext} from 'react';

/* Used to create useContext hooks that through a relevant error if misused. */
export function createContextHook<V>(contextObj: {[key: string]: Context<V>}) {
  return function () {
    const name = Object.keys(contextObj)[0] as keyof typeof contextObj;
    const context = useContext<V>(contextObj[name]);

    if (!context) {
      throw new Error(
        `This component is not used within it's context: ${name}`,
      );
    }

    return context;
  };
}
